.main-header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: var(--header-height);
  padding: 0 var(--padding);
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba($body-bg,0);
  z-index: var(--z-header);
}
.main-title, .company-name{
  font-size: 1.5rem;
}
.main-title{
  text-transform: uppercase;
  font-weight: 700;
  transform: translateY(-25%);
  a{
    text-indent: -1000px;

  }
  img{
    width: auto;
    height: .85em;
  }
}
.company-name{
  color: var(--color-title);
}
