.home{
  position: relative;
  height: 100vh;
  width: 100%;
  img{
    opacity: 0;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: opacity .5s;
    &.active{
      opacity: 1;
    }
  }
}

.desktop .home{
  height: calc(100vh + 3px) !important;
}
