.main-nav{
  height: 100vh;
  position: relative;
}
.main-menu{
  @extend %lists-style;

  .desktop &{
    position: fixed;
    bottom: 0;
    left: 10%;
    transform-origin: center;
    width: calc(100vh - var(--header-height) - 1em);
    transform: rotate(-90deg) translateY(0%) translateX(49%);
    display: flex;
    justify-content: space-between;
    padding: 0 2em;
    @include from(1400px){
      left: 8%;
    }
    li:nth-child(1){
      order: 4;
    }
    li:nth-child(2){
      order: 3;
    }
    li:nth-child(3){
      order: 2;
    }
    a{
      font-size: 1.3em;
      text-transform: uppercase;
      //line-height: 1.1;
    }
    li.active, a.active{
      font-weight: 700;
    }
  }
}
