.desktop {
  & .contact{
    &-container{
      width: 100%;
      height: 75vh;
      display:flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
      padding-bottom: 5vh;
    }
    span{
      display: block;
    }
  }
  & .contact__wrapper{
    width: 100%;
    display: grid;
    grid-template-columns: 40% 1fr;
    justify-content: start;
    font-size: 1.2em;
    .phone{
      font-size: 1.3em;
    }
  }
  .showMap{
    font-size: 1.2em;
  }
}

.contact ul{
  $size: 1em;
  @extend %lists-style;
  display: flex;
  li{
    margin-right: 1em;
    @include from(l){
      margin-right: 4vw;
    }
    &:last-child{
      margin-right: 0;
    }
  }
  a{
    display: flex;
    width: auto;
    height: $size;
  }
  img{
    height: 100%;
    width: auto;
  }
}
