
.section-wrapper{
  display: grid;
  grid-template-columns: 35% 65%;
  grid-gap: 1em;
  width: 100vw;
  .mobile{
    display: block;
  }
}
.main{
  height: 100vh;
  position: relative;
}
.section-page{
  height: 100vh;
  width: 100%;
  font-size: 1rem;
  @media screen and (min-height: 800px){
    font-size: 1.1rem;
  }
  letter-spacing: 1px;
  text-align: justify;
  &:not(.home){
    position: relative;
    padding-top: var(--header-height);
    padding-left: 1em;
    padding-right: var(--padding);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    &::before{
      content: '';
      display: block;
      position: absolute;
      width: calc(100% - 1em);
      height: 1px;
      background: $color-font;
      top: var(--header-height);
      right: 0;
    }
  }
}
$border: 1px solid $color-font;
.section__title{
  display: inline-block;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 2em;
  margin-bottom: 1em;
}
.section__subtitle{
  margin-bottom: 1rem;
}
.bullet{
  $size: .7em;
  position: relative;
  padding: 0;
  &:before{
    content: '';
    position: absolute;
    height: $size;
    width: $size;
    background: #63CCF3;
    border-radius: 50%;
    left: -$size;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
  }
}
.section__list{
  list-style: circle;
  li{
    margin-bottom: .2em;
    &:last-child{
      margin-bottom: 0;
    }
  }
}
.line{
  display: table;
  padding-bottom: 1rem;
  border-bottom: $border;
  line-height: 1;
}

.modal{
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba($body-bg,80);
  height: 100vh;
  width: 100%;
  z-index: var(--z-modal);
  img{
    width: 2vw;
    height: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  iframe{
    position: absolute;
    height: 600px;
    max-height: 60%;
    width: 80%;
    max-width: 800px;
    border: none;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  &_close{
    $size: 2em;
    position: absolute;
    width: $size;
    height: $size;
    top: $size*2;
    right: $size*2;
    cursor: pointer;
    .mobile &{
      top: $size;
      right: $size;
    }
    &::before, &::after{
      content: '';
      display: block;
      position: absolute;
      height: 100%;
      width: 2px;
      left: 50%;
      transform-origin: center;
      background: $color-font;
      transform: rotate(45deg);
    }
    &:after{
      transform: rotate(-45deg);
    }
  }
}
.desktop .section-page{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: $body-bg;
  transform: translateY(100%);
  transition: transform .5s;
  &.active{
    transform: translateY(0);
    z-index: var(--z-fixed);
  }
}
