*,
*::before,
*:after {
  box-sizing: border-box;
}

img{
  width: 100%;
  height: auto;
  display: block;
}

html,body,section,article,header,footer,main,h1,h2,h3,h4,h5,h6,p{
  margin: 0;
}
section,article,main,header,footer,main,h1,h2,h3,h4,h5,h6,p{
  font-weight: normal;
  line-height: 1;
}

a{
  display: inline-block;
  text-decoration: none;
  color: inherit;
  &:hover{
    text-decoration: none;
  }
}
p{
  margin-bottom: 1em;
  &:only-child, &:last-child{
    margin-bottom: 0;
  }
}

body {
  font-size: 16px;
  font-family : $font-family;
  background: $body-bg;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  color: $color-font;
}
body.desktop{
  height: 100vh;
  width: 100vw;
  overflow-y: hidden;
  overflow-x: hidden;
}
