.portfolio {
  position: relative;
  & .section__title {
    position: absolute;
    top: calc(var(--header-height) + 2em);
  }
}

$height: 35vh;
.portfolio-wrapper {
  display: grid;
  grid-template-columns: 15% 1fr;
  grid-gap: 5vw;
  width: 100%;
  //max-height: var(--maxHeight);
  height: calc(100vh - (var(--header-height) + 13em));
  background: $body-bg !important;
}

.portfolio {
  --maxHeight: 50vh;
  @media screen and (min-height: 800px) {
    --maxHeight: 60vh;
  }

  &__group {
    margin-bottom: 1em;
  }

  &__menu {
    max-height: var(--maxHeight);
    width: 300px;
  }

  &__list-title {
    text-transform: uppercase;
    font-size: 1.2em;
    margin-bottom: 0.5em;

    &.active {
      font-weight: 700;
    }
  }

  &__list {
    @extend %lists-style;
    display: none;

    // &.active{
    //   display: block;
    // }
    li {
      line-height: 1.6em;
      cursor: pointer;
      &:last-child {
      }
    }
  }
}

.portfolioShow {
  height: calc(100vh - (var(--header-height) + 13em));
  &__container {
    display: grid;
    grid-template-columns: 30% 1fr;
    grid-gap: 5vw;
    height: inherit;
  }
  &__imgWrap {
    height: inherit;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
  }
  &__img {
    width: auto;
    height: inherit;
    display: block;
    transform-origin: top center;
    transform: scaleY(0.95);
  }
  &__data {
    font-family: $font-tequila;
    height: inherit;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    transform-origin: bottom left;
    padding-top: 25%;
    //transform: scale(.9);
    padding-bottom: 1.3rem;
    @media screen and (max-height: 800px) {
      transform-origin: bottom left;
      transform: scale(0.85);
      padding-top: 0;
    }
  }
  &__title {
    margin-bottom: 1.5rem;
    h2 {
      font-family: $font-tequilas-heading;
      text-transform: uppercase;
      margin: 0;
      font-size: 2.2vw;
      line-height: 1;
      color: #111;
    }
    img {
      height: 5vh !important;
      width: auto important;
    }
  }
  &__body {
    font-size: 0.8em;
    p {
      margin-bottom: 0.8em;
      &:last-child,
      &:last-of-type {
        margin-bottom: 0;
      }
    }
    span,
    a {
      font-weight: 700;
    }
  }
  &__footer {
    $size: 1em;
    margin: 0.8em 0 0;
    font-size: 0.8em;
    ul {
      @extend %lists-style;
      display: flex;
      margin-bottom: 0.8em;
      li {
        margin-right: $size;
        &:last-child {
          margin-right: 0;
        }
      }
      a {
        display: inline-block;
        width: auto;
        height: $size;
      }
      img {
        width: auto;
        height: 100%;
      }
    }
    a {
      font-weight: 700;
    }
  }
}
.no-display {
  display: none;
}
.portfolioShow__img {
  transform-origin: bottom right;
  transform: scaleY(1);
  width: auto !important;
}
.portfolioShow__imgWrap {
  transform-origin: bottom right;
  transform: scale(1.1);
}

.anselmo .portfolioShow__imgWrap {
  transform: scale(1);
}
.anselmo .portfolioShow__img {
  transform-origin: bottom right;
  transform: scaleY(0.86);
}
.portfolio__list-title {
  cursor: pointer;
}
